.contact-wrapper {
	padding-bottom: 100px;

	@media (max-width: 1024px) {
		padding-bottom: 0;
	}

	.row {
		justify-content: center;
	}

	.contact-title {
		font-size: 25px;
		font-weight: 700;
		text-transform: uppercase;
		margin-bottom: 40px;

		@media (max-width: 1199px) {
			margin-bottom: 25px;
			font-size: 21px;
		}

		@media (max-width: 767px) {
			margin-bottom: 10px;
			font-size: 16px;
		}
	}

	#map {
		height: 360px;
	}

	.contact-information-wrapper {
		padding-top: 100px;

		@media (max-width: 1024px) {
			padding-top: 30px;
		}
	}

	.contact-form {
		p {
			font-size: 16px;
			color: #707070;
			margin-bottom: 15px;
		}

		.form-button {
			button {
				margin-left: auto;
				@media (max-width: 1024px) {
					margin-right: auto;
				}
			}
		}

		@media (max-width: 1199px) {
			p {
				font-size: 15px;
			}
		}

		@media (max-width: 1024px) {
			margin-bottom: 20px;
		}

		@media (max-width: 767px) {
			p {
				font-size: 14px;
			}
		}
	}

	.contact-list {
		background: #F9F9F9;
		padding: 25px;
		font-size: 20px;
		font-weight: 300;
		font-family: 'Roboto', sans-serif;

		> p {
			margin-bottom: 30px;
		}

		hr {
			margin: 0 0 30px 0;
			border: 0;
			border-top: 1px solid #777777;

			@media (max-width: 1024px) {
				margin: 0 0 20px 0;
			}
		}

		.contact-item {
			margin-bottom: 30px;

			h3 {
				font-weight: 400;
			}

			@media (max-width: 1024px) {
				margin-bottom: 20px;
			}
		}

		.contact-information {
			strong {
				font-weight: 400;
				margin-right: 5px;
			}
		}

		@media (max-width: 1199px) {
			font-size: 17px;
		}

		@media (max-width: 1024px) {
			margin: 0 -10px;
			padding: 10px;
		}
		@media (max-width: 767px) {
			font-size: 14px;
		}
	}

	@media (max-width: 1024px) {
		#map {
			height: 190px
		}
	}
}